body {
  background-color: #fae1f4;
}

.breed-select {
  width: 160px;
  height: 34px;
  line-height: 30px;
  display: inline-block;
  position: relative;
}

.cat-info {
  width: 1360px;
  background-color: #edfffd;
  border-radius: 30px;
  margin: 50px;
  display: flex;
  box-shadow: 0 2px 18px rgba(0, 0, 0, .3);
}

.breed-image {
  width: 800px;
  border-radius: 30px 0 0 30px;
}

.breed-container {
  width: 500px;
  padding: 20px;
}

.breed-name {
  color: #cc0060;
  font-family: Bagel Fat One, cursive;
  font-size: 56px;
}

.breed-temperament {
  color: #603;
  font-family: Montserrat, sans-serif;
  font-size: 30px;
  font-weight: 600;
  line-height: 1.5;
}

.breed-description {
  color: #404040;
  font-family: Montserrat, sans-serif;
  font-size: 24px;
  line-height: 1.5;
}

.loader {
  width: 100px;
  height: 100px;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, .3);
}

.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #ff3d00;
}

